import React from "react";

const  Infos =[
    {
        id:1,
        img:"Landing_Page_icon_(by_Raven_Rebellious).png",
        title:"Landing page",
        text:"         ",
        sena:' 40 000 '
    },
    {
        id:2,
        img:"_by_R_R.png",
        title:" \r\n",
        text:" ,     ,  , ,   , -,  .",
        sena:' 50 000 \r\n'

    },
    {
        id:3,
        img:"__by_R_R.png",
        title:" \r\n",
        text:"          .         ,        .            .",
        sena:' 70 000 '

    },
    {
        id:4,
        img:"__by_R_R.png",
        title:" \r\n",
        text:"  --   , -,       .   , ,    .           .\r\n",
        sena:' 90 000 \r\n'

    },
    {
        id:5,
        img:"Directory_icon_v2(by_Raven_Rebellious).png",
        title:"-\r\n",
        text:"-,        ,    .          . -    ,     .      -          ",
        sena:' 120 000 \r\n'

    },
    {
        id:6,
        img:"Store_icon_(by_Raven_Rebellious).png",
        title:" \r\n",
        text:" -    ,    ,     ,    ,      ..",
        sena:' 150 000 \r\n'

    },

];

const infoLost = Infos.map(info =>{
    return(
        <div key={info.id} className="col-md-4 site-ops wow bounceInLeft" id="" data-wow-duration="1.5s"
             data-wow-delay="0.7s" data-wow-offset="100">
            <img src={"/img/"+info.img} className="rounded mx-auto d-block"
                 alt="     IT "/>
            <div className="h-ops">
                <h3>{info.title}</h3>
            </div>
            <div className="text-ops">
                {info.text}
            </div>
            <div className="sena-ops" id="red-ops">
                {info.sena}
            </div>
        </div>
    )
})

export default class SiteBlok extends React.Component{


    render(){
        return(
            <section className="site-dosk my-5">
                <div className="blok_fon"></div>
                <div className="container">
                    <div className="row">

                        {infoLost}

                    </div>


                </div>
            </section>
        )
    }
}


